import ReactDOM from 'react-dom';
import './index.css';

let renderCount=0;

function handleClick() {
    console.log('button was clicked');
}

function render() {
    renderCount++;
    const listContents = [3, 1, 4, 5, 9];
    listContents.splice(renderCount % 6, 0, 88)
    console.log(listContents);
    ReactDOM.render(
        <div>
            <ul>
                {listContents.map(
                    e => <li key={e}> {`list item ${e}`} </li>)}
            </ul>
            <button onClick={handleClick}>
                Press me!
            </button>
        </div>,
        document.getElementById('root')
    );
}

render();
setInterval(render, 2000);
